@import "../../inc/vars";
.acro-iconbox-block {
	color: #4F4B58;
	h2 {
		@include pop(400, 30, 1.24);
		margin-bottom: 32px;
		text-align: center;
		@media (min-width: $screen-sm){
			margin-bottom: 40px;
		}
	}
	h3 {
		@include pop(400, 24, 1.24);
		color: #5A6272;
		margin-bottom: 16px;
		text-align: center;
		@media (min-width: $screen-sm){
			margin-bottom: 12px;
		}
	}
	.acro-iconbox-block__title__description {
		text-align: center;
		@include fig(400, 20, 1.56);
		color: #4F4B58;
		margin-bottom: 40px;
		@media (min-width: $screen-sm){
			margin-bottom: 68px;
		}
	}
	.list-item__link {
		text-decoration: none;
		@include pop(500, 13, 1.24);
		text-transform: uppercase;
		color: $purple;
	}
	.list-item {
		&__title {
			@include pop(400, 24, 1.36);
			margin-bottom: 8px;
			text-align: left;
		}
		&__description {
			@include fig(400, 16, 1.36);
		} 
	}
	&.style-1 {
		padding: 56px 20px 40px 20px;
		@media (min-width: $screen-sm){
			padding: 56px 40px 40px 40px;
		}
		.acro-iconbox {
			&-block {
				&__title {
					text-align: center;
				}
			}
			&__content {
				&__items {
					display: grid;
					grid-template-columns: 1fr;
					gap: 20px;
					@media (min-width: $screen-sm){
						grid-template-columns: 1fr 1fr;
					}
					@media (min-width: $screen-md){
						grid-template-columns: 1fr 1fr 1fr;
					}
					.list-item {
						padding: 20px 8px 65px 8px;
						box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
						position: relative;
						color: #4F4B58;
						.listContent {
							padding: 0 12px;

						}
						&__link {
							position: absolute;
							bottom: 20px;
							left: 20px;
						}
					}
				}
			}
		}
	}
	&.style-2,
	&.style-5 {
		padding: 56px 20px 40px 20px;
		border-bottom: 1px solid #D1D5DB;
		@media (min-width: $screen-sm){
			padding: 56px 40px 40px 40px;
		}
		h2 {
			font-size: 34px;
			text-align: center;
			margin-bottom: 12px;
			@media (min-width: $screen-sm){
				font-size: 36px;
			}
		}
		.acro-iconbox__content {
			overflow-x: auto;
			.acro-iconbox__content__items {
				display: grid;
				grid-template-columns: 1fr 1fr 1fr 1fr;
				gap: 32px;
				min-width: 1000px;
				.list-item {
					position: relative;
					padding-left: 26px;
					@media (min-width: $screen-sm){
						margin-bottom: 30px;
					}
					&:before {
						content: '';
						display: block;
						width: 1px;
						height: 100%;
						background: #BABFC9;
						position: absolute;
						left: 3px;
						top: 0;
					}
					&:after {
						content: '';
						display: block;
						width: 8px;
						height: 8px;
						background: $purple;
						border-radius: 50%;
						position: absolute;
						left: 0px;
						top: 0;
					}
					&__title {
						color: $purple;
						font-size: 22px;
						line-height: 1.36;
						margin-bottom: 8px;
						@media (min-width: $screen-sm){
							margin-bottom: 4px;
						}
					}
					&__description {
						font-size: 18px;
						line-height: 1.56;
					}
				}
			}
		}
	}
	&.style-3 {
		padding: 56px 20px 40px 20px;
		@media (min-width: $screen-sm){
			padding: 56px 40px 40px 40px;
		}
		h2 {
			font-size: 34px;
			text-align: center;
			margin-bottom: 12px;
			@media (min-width: $screen-sm){
				font-size: 36px;
			}
		}
		.acro-iconbox-block__title__description {
			margin-bottom: 16px;
			@media (min-width: $screen-sm){
				margin-bottom: 32px;
			}
		}
		.acro-iconbox__content {
			display: grid;
			grid-template-columns: 1fr;
			align-items: center;
			@media (min-width: $screen-md){
				grid-template-columns: 1fr 1fr;
				gap: 60px;
			}
			&__image {

			}
			&__items {
				display: grid;
				gap: 16px;
				@media (min-width: $screen-md){
					padding-right: 30px;
				}
				.list-item {
					padding: 24px 24px 20px 24px;
					background: #FFF;
					box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
					position: relative;
					&__image {
						width: 32px;
						position: absolute;
						left: 24px;
						top: 20px;
					}
					&__title {
						padding-left: 40px;
						font-size: 20px;
						line-height: 1.36;
						color: $purple;
						margin-bottom: 12px;
					}
				}
			}
		}
	}
	&.style-5 {
		border: none;
		padding: 0 22px;
		width: 1440px;
		max-width: 100%;
		margin: 0 auto;
		@media (min-width: $screen-sm){
			padding: 0 40px;
		}
		.acro-iconbox__content {
			overflow: visible !important;
			&__items {
				display: grid;
				grid-template-columns: 1fr !important;
				gap: 30px !important;
				min-width: 100% !important;
				@media (min-width: $screen-sm){
					grid-template-columns: 1fr 1fr !important;
				}
				@media (min-width: $screen-md){
					grid-template-columns: 1fr 1fr 1fr !important;
				}
				.list-item {
					background: #fff;
					padding: 22px 40px !important;
					margin: 0 !important;
					box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
					@media (min-width: $screen-sm){
						padding: 30px 50px !important;
					}
					&:before,
					&:after {
						display: none !important;
					}
					.list-item__image {
						margin-bottom: 6px;
						@media (min-width: $screen-sm){
							margin-bottom: 11px;
						}
						img {
							width: 49px;
						}
					}
					.listContent {
						.list-item__title {
							@include titleGrad();
							@include pop(400, 22, 1.24);
							margin-bottom: 20px;
							@media (min-width: $screen-sm){
								font-size: 26px;
								margin-bottom: 32px;
							}
						}
						.list-item__description {
							@include fig(400, 16, 1.56);
							padding-left: 22px;
							position: relative;
							@media (min-width: $screen-sm){
								padding-left: 30px;
								font-size: 17px;
							}
							&:before {
								content: '';
								display: block;
								width: 1px;
								height: calc(100% - 5px);
								background: #BABFC9;
								position: absolute;
								left: 2px;
								top: 5px;
								@media (min-width: $screen-sm){
									left: 3px;
								}
							}
							&:after {
								content: '';
								display: block;
								width: 6px;
								height: 6px;
								background: $purple;
								border-radius: 50%;
								position: absolute;
								left: 0;
								top: 5px;
								@media (min-width: $screen-sm){
									width: 8px;
									height: 8px;
								}
							}
						}
					}
				}
			}
		}
	}
	&.style-6 {
		display: flex;
		flex-direction: column;
		padding: 0 20px;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto 97px auto;
		@media (min-width: $screen-md){
			padding: 0 100px;
		}
		@media (min-width: $screen-sm){
			flex-direction: row;
			margin-bottom: 167px;
		}
		.acro-iconbox-block__title {
			width: 100%;
			@media (min-width: $screen-sm){
				width: 40%;
				padding-right: 40px;
			}
			h2 {
				text-align: center;
				@include titleGrad();
				@include pop(400, 30, 1.24);
				margin-bottom: 24px;
				@media (min-width: $screen-sm){
					text-align: left;
					margin-bottom: 30px;
					font-size: 36px;
				}
			}
			&__description {
				text-align: center;
				@include fig(400, 20, 1.56);
				margin-bottom: 38px;
				@media (min-width: $screen-sm){
					text-align: left;
					margin-bottom: 0;
					font-size: 24px;
				}
			}
		}
		.acro-iconbox__content {
			width: 100%;
			@media (min-width: $screen-sm){
				width: 60%;
				padding-left: 40px;
			}
			&__items {
				display: grid;
				grid-template-columns: 1fr 1fr 1fr;
				grid-row-gap: 30px;
				grid-column-gap: 40px;
				overflow-x: auto;
				@media (min-width: $screen-sm){
					grid-row-gap: 25px;
					grid-column-gap: 30px;
				}
				.list-item {
					min-width: 55vw;
					@media (min-width: $screen-sm){
						min-width: 150px;
					}
					.list-item__image {
						width: 37px;
						margin-bottom: 17px;
						@media (min-width: $screen-sm){
							width: 31px;
							margin-bottom: 14px;
						}
					}
					.listContent {
						.list-item__title {
							color: $purple;
							@include pop(400, 22, 1.36);
							letter-spacing: -0.438px;
							margin-bottom: 17px;
							@media (min-width: $screen-sm){
								font-size: 18px;
								letter-spacing: -0.36px;
								margin-bottom: 14px;
							}
						}
						.list-item__description {
							@include pop(400, 17, 1.36);
							@media (min-width: $screen-sm){
								font-size: 14px;
							}
						}
					}
				}
			}
		}
	}
	&.style-7 {
		padding: 0 20px;
		width: 1400px;
		max-width: 100%;
		margin: 0 auto 59px auto;
		@media (min-width: $screen-sm){
			margin-bottom: 157px;
		}
		.acro-iconbox-block__title {
			margin-bottom: 64px;
			@media (min-width: $screen-sm){
				margin-bottom: 76px;
			}
			h2 {
				text-align: center;
				@include titleGrad();
				@include pop(400, 30, 1.24);
				margin-bottom: 24px;
				@media (min-width: $screen-sm){
					margin-bottom: 30px;
					font-size: 36px;
				}
			}
		}
		.acro-iconbox__content__items {
			display: flex;
			flex-direction: column;
			position: relative;

			.list-item {
				display: flex;
				justify-content: flex-end;
				&:last-child {
					.listContent {
						padding-bottom: 0 !important;
						&:before {
							display: none;
						}
					}
				}
				.list-item__image {
					padding-right: 13px;
					position: relative;
					&:before {
						content: '';
						display: block;
						position: absolute;
						width: 10px;
						height: 10px;
						border-radius: 50%;
						background: $purple;
						top: 0;
						right: -5px;
						z-index: 2;
					}
					@media (min-width: $screen-sm){
						padding-right: 23px;
					}
					img {
						width: 40px;
						margin-top: -5px;
					}
					@media (min-width: $screen-sm){
						img {
							width: 48px;
						}
					}
				}
				.listContent {
					width: 50%;
					padding-left: 13px;
					position: relative;
					padding-bottom: 30px;
					@media (min-width: $screen-sm){
						padding-bottom: 40px;
					}
					.list-item__title {
						@include pop(400, 18, 1.36);
						color: $purple;
						margin-bottom: 11px;
						@media (min-width: $screen-sm){
							font-size: 24px;
							margin-bottom: 18px;
						}
					}
					.list-item__description {
						@include fig(400, 14, 1.56);
						@media (min-width: $screen-sm){
							font-size: 19px;
						}
					}
					&:before {
						content: '';
						display: block;
						width: 2px;
						height: 100%;
						position: absolute;
						top: 0;
						left: -1px;
						background: #BABFC9;
					}
					@media (min-width: $screen-sm){
						padding-left: 23px;
					}
				}
				&:nth-child(even){
					justify-content: flex-start;
					text-align: right;
					.list-item__image {
						order: 2;
						padding-left: 13px;
						padding-right: 0;
						&:before {
							left: -5px;
							right: auto;
						}
						@media (min-width: $screen-sm){
							padding-left: 23px;
						}
					}
					.listContent {
						padding-right: 13px;
						padding-left: 0;
						&:before {
							left: auto;
							right: -1px;
						}
						@media (min-width: $screen-sm){
							padding-right: 23px;
						}
						.list-item__title {
							text-align: right;
						}
					}
				}
			}
		}
	}
}